import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import ReactDOM from 'react';

const styles = theme => ({
  board: {
    height: "100%",
    width: "100%",
    border: "1px solid #999",
  },
  boardHeaderRow: {
    height: "10%",
  },
  boardRow: {
    height: "18%",
  },
  headerSquare: {
    [`@media(max-height: 450px) and (orientation: landscape)`]: {
      fontSize: "4vw",
    },
    [`${theme.breakpoints.down('xs')} and (orientation: portrait)`]: {
      fontSize: "4vw",
    },
    fontSize: "4vh",
    fontWeight: "bold",
    width: "20%",
    height: "100%",
    background: "#fff",
    border: "1px solid #999",
    padding: "0",
    textAlign: "center",
    backgroundColor:"white",  
  },
  square: {
    [`@media(max-height: 450px) and (orientation: landscape)`]: {
      fontSize: "10vw",
    },
    [`${theme.breakpoints.down('xs')} and (orientation: portrait)`]: {
      fontSize: "10vw",
    },
    fontSize: "8vh",
    fontFamily: "Arial",
    fontWeight: "bold",
    width: "20%",
    height: "100%",
    background: "#fff",
    border: "1px solid #999",
    padding: "0",
    textAlign: "center",
    backgroundColor:"white",
  },
});

class Square extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      bgColor: ""
    }
  }

  boxClick = (e) => {
    this.setState({
      bgColor: this.state.bgColor === "#FF6666" ? "white" : "#FF6666"
    })
  }

  render() {
    const { classes } = this.props;

    return (
      <button 
        className={classes.square}
        style={{backgroundColor: this.state.bgColor}}
        onClick={this.boxClick}>
        {this.props.value}
      </button>
    );
  }
}

class Board extends React.Component {
  renderSquare(i) {
    const StyledSquare = withStyles(styles)(Square);
    return <StyledSquare value={i} />;
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.board}>
        <div className={classes.boardHeaderRow}>
          <button className={classes.headerSquare}>B</button>
          <button className={classes.headerSquare}>I</button>
          <button className={classes.headerSquare}>N</button>
          <button className={classes.headerSquare}>G</button>
          <button className={classes.headerSquare}>O</button>
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
          {this.renderSquare(9)}
          {this.renderSquare(10)}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(11)}
          {this.renderSquare(12)}
          {this.renderSquare("FR")}
          {this.renderSquare(14)}
          {this.renderSquare(15)}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(16)}
          {this.renderSquare(17)}
          {this.renderSquare(18)}
          {this.renderSquare(19)}
          {this.renderSquare(20)}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(21)}
          {this.renderSquare(22)}
          {this.renderSquare(23)}
          {this.renderSquare(24)}
          {this.renderSquare(25)}
        </div>
      </div>
    );
  }
}


export default withStyles(styles)(Board)
